<template>
    <div class="container">
        <c-form ref="form" :form="form" :rules="rules" class="form">
            <c-form-item>
              <c-input v-model="form.name" type="text" placeholder="" />
            </c-form-item>
            <c-form-item>
              <c-input v-model="form.nickName" type="text" placeholder="" />
            </c-form-item>
            <c-form-item>
              <c-input v-model="form.password" type="password" placeholder="" />
            </c-form-item>
            <c-form-item>
              <c-input v-model="form.confirmPassword" type="password" placeholder="" />
            </c-form-item>
            <c-form-item>
              <c-button @click="handleSubmit"></c-button>
            </c-form-item>
        </c-form>
        <p class="notice">
          <router-link to='/login'></router-link>
        </p>
    </div>
</template>
<script>
import { userRegister } from '@/services/user'
export default {
  data () {
    return {
      form: {
        name: '',
        nickName: '',
        password: '',
        confirmPassword: ''
      },
      rules: {
        name: [{ required: true, message: '' }],
        nickName: [{ required: true, message: '' }],
        password: [{ required: true, message: '' }],
        confirmPassword: [{ required: true, message: '' }]
      }
    }
  },
  methods: {
    handleSubmit () {
      this.$refs.form.validate(error => {
        if (error) {
          return this.$message.error(error[0].message)
        }
        if (this.form.password !== this.form.confirmPassword) return this.$message.error('')
        this.register()
      })
    },
    async register () {
      await userRegister(this.form)
      this.$message.success('')
      this.$router.replace('/login')
    }
  }
}
</script>
<style lang="less" scoped>
.container{
  width: 750/@r;
  margin: 0 auto;
  min-height: 100vh;
  box-shadow: 0 0 5px #999999;
}
.form{
    padding-top: 80/@r;
}
.notice{
  padding: 0 24/@r;
  text-align: right;
  font-size: 26/@r;
}
</style>
