import Vue from 'vue'

export default (Comp, props) => {
  const Contructor = Vue.extend(Comp)
  // 1. 
  const comp = new Contructor({ propsData: props })
  // 2. 
  comp.$mount()
  // 3. bodydom
  document.body.appendChild(comp.$el)
  // 4. 
  comp.remove = function () {
    comp.$destroy()
    document.body.removeChild(comp.$el)
  }

  return comp
}
